#cover {
    height: 100vh;
    width: 100vw;
    display: table-cell;
    vertical-align: middle;
    position: relative;
    z-index: 9999;
}

#cover ul {
    display: inline-block;
    list-style: none;
}

.particles-background {
    position: absolute;
    top: 0;
    left: 0;
    opacity: .1;
    z-index: 1;
}
