h1, h2 {
    color: white;
    margin: 0;
}

h1 {
    font-size: 25pt;
}

strong {
    color: dodgerblue;
}

.carousel-animation {
    animation: 1.5s ease-out opacity infinite;
    animation-direction: alternate;
}

@keyframes opacity {
    0% {
        opacity: 0;
        visibility: 0;
    }
    100% {
        opacity: 1;
        visibility: 1;
    }
}