#vertical-navbar {
    float: right;
}

#vertical-navbar ul li{
    transition-duration: .2s;
    margin: 30px;
    filter: invert(40%) sepia(5%) saturate(9%) hue-rotate(318deg) brightness(95%) contrast(79%); /* dimgray */
    display: block;
}

#vertical-navbar ul li:hover {
    transition-duration: .2s;
    filter: invert(100%) sepia(97%) saturate(0%) hue-rotate(79deg) brightness(105%) contrast(104%); /* white */
}